.Pokecard{
   background-color: white;
   width: 300px;
   padding: 10px;
   margin: 1rem;
   box-shadow: 7px 10px 12px -5px rgba(0,0,0,0.56);
   text-align: center;
   border-radius: 5px;
}

.Pokecard-data{
   font-size: 80%;
}

.Pokecard-title {
   font-size: 80%;
   font-weight: bold;
   font-size: 1.5em;
}

.Pokecard-image {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 250px;
   background-color: #eceff1;
   border-radius: 5px;
   margin-bottom: 1rem;
}

.Pokecard img {
   transition: all ease 500ms;
}

.Pokecard img:hover {
   transform: scale(1.25) ;
   opacity: 0.7;
}